.footer {
    background-color: #FFF5F6;

    &__link {
        color: $accent-color;
    }

    &__text {
        font-family: 'Titan One';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 1.17;
        text-transform: uppercase;
        margin-bottom: 16px;
        color: $secondary-color;
    }

    &__text--colored {
        font-family: 'Titan One';
        font-style: normal;
        font-weight: 400;
        font-size: 34px;
        line-height: 1.15;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        margin-bottom: 40px;
        color: $accent-color;
    }

    &__contact-block {
        margin-bottom: 40px;
    }

    &__contact {
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        line-height: 186%;
        text-align: right;
        color: #000000;
    }

    &__copyright {
        border-top: 1px solid #E1E1E1;
        display: flex;
        justify-content: center;
        background-color: #FFF5F6;
        padding: 8px 0;
    
        &-text {
            font-family: 'DM Sans';
            font-style: normal;
            font-weight: 700;
            font-size: 14px;
            line-height: 152%;
            color: #907E82;
        }
    }
}

.btn-red--size {
    padding: 9px 30px 9px 17px;
    margin-bottom: 10px;
}

.btn-white--size {
    padding: 9px 50px 9px 30px;
}

.socials {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 40px;

    &__item:not(:last-child) {
        margin-right: 28px;
    }

    &__link {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background: #FEA5BB;
    }

    &__icon {
        width: 19px;
        height: 19px;
        fill: $main-white-color;
    }
}

@media screen and (max-width: 767px) {
    .footer {

        &__text-block {
            max-width: 232px;
        }

        &__copyright {
            padding: 16px 20px 20px;

            &-text{
                text-align: center;
            }

            &-text span {
                display: block;
            }
        }

        &__buttons {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 48px;
        }
    }
}

@media screen and (min-width: 768px) and (max-width: 1200px) {
    .footer {

        &__text-block {
            max-width: 340px;
        }
        
        &__text {
            font-family: 'Titan One';
            font-style: normal;
            font-weight: 400;
            font-size: 26px;
            line-height: 30px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: $secondary-color;
        }

        &__text--colored {
            font-family: 'Titan One';
            font-style: normal;
            font-weight: 400;
            font-size: 48px;
            line-height: 55px;
            letter-spacing: 0.06em;
            text-transform: uppercase;
            color: $accent-color;
        }

        &__buttons {
            display: flex;
            justify-content: center;
            align-items: baseline;
            margin-bottom: 40px;
        }

        &__block {
            display: flex;
            justify-content: space-between;
            margin-bottom: 40px;
        }

        .btn-red--size {
            margin-right: 15px;
        }
    }
}

@media screen and (min-width: 1201px) {
    .footer {

        &__text {
            font-family: 'Titan One';
            font-style: normal;
            font-weight: 400;
            font-size: 30px;
            line-height: 34px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: $secondary-color;
        }

        &__text--colored {
            font-family: 'Titan One';
            font-style: normal;
            font-weight: 400;
            font-size: 58px;
            line-height: 66px;
            letter-spacing: 0.06em;
            text-transform: uppercase;
            color: $accent-color;
        }

        &__buttons {
            display: flex;
            justify-content: center;
            align-items:baseline;
            margin-bottom: 120px;
        }

        &__block {
            display: flex;
            justify-content: space-between;
            margin-bottom: 60px;
        }

        &__contact-block {
            align-self: flex-end;
        }

        .btn-red--size {
            margin-right: 15px;
        }
    }
}
